    import React, { useState, useEffect, useRef } from 'react';
    import LoginBox from '../boxes/LoginBox';
    import SignUpBox from '../boxes/SignUpBox';
    import Navbar from '../components/Navbar';
    import PasswordBox from '../boxes/PasswordBox';
    import EmailBox from '../boxes/EmailBox';
    import MobileNavbar from '../boxes/MobileNavbar';
    import { useNavigate } from 'react-router-dom';
    import { auth,  } from '../firebase'; 
    import { onAuthStateChanged } from "firebase/auth";

    function BecomeAnExpert() {
    const [showLoginBox, setShowLoginBox] = useState(false);
    const [showSignUpBox, setShowSignUpBox] = useState(false);
    const [showPasswordBox, setShowPasswordBox] = useState(false);
    const [showEmailBox, setShowEmailBox] = useState(false);
    const [isPopupOpen, setIsPopupOpen] = useState(false);
    
    const navigate = useNavigate();
    const popupRef = useRef(null);

    const closeAllPopups = () => {
        setShowLoginBox(false);
        setShowSignUpBox(false);
        setShowPasswordBox(false);
        setShowEmailBox(false);
        setIsPopupOpen(false); 
    };

    const showSignUp = () => {
        closeAllPopups();
        setShowSignUpBox(true);
    };

    const showPassword = () => {
        closeAllPopups();
        setShowPasswordBox(true);
    };

    const showEmail = () => {
        closeAllPopups();
        setShowEmailBox(true);
    };

    const handleLoginButtonClick = () => {
        closeAllPopups();
        setShowLoginBox(true);
    };
    
    useEffect(() => {
        const handleKeyPress = (event) => {
        if (event.key === 'Escape') {
            closeAllPopups();
        }
        };

        const handleClickOutside = (event) => {
        if (
            popupRef.current &&
            !popupRef.current.contains(event.target) &&
            event.target.getAttribute('id') !== 'loginButtonNav'
        ) {
            closeAllPopups();
        }
        };

        window.addEventListener('keydown', handleKeyPress);
        document.addEventListener('mousedown', handleClickOutside);

        return () => {
        window.removeEventListener('keydown', handleKeyPress);
        document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    // Check if the screen width is less than or equal to 768px (mobile devices)
    const isMobile = window.innerWidth <= 768;

    useEffect(() => {
        // Disable scrolling on mobile devices when a popup is open
        if (isMobile) {
        document.body.style.overflow = showLoginBox || showSignUpBox || showPasswordBox || showEmailBox
            ? 'hidden'
            : 'auto';
        }

        return () => {
        // Enable scrolling when popups are closed
        if (isMobile) {
            document.body.style.overflow = 'auto';
        }
        };
    }, [showLoginBox, showSignUpBox, showPasswordBox, showEmailBox, isMobile]);
  


    useEffect(() => {
        const unsubscribe = onAuthStateChanged(auth, (user) => {
          if (user) {
            // User is logged in, redirect to the root ("/")
            navigate('/ExpertVerificationPage');
          }
        });
    
        return () => {
          // Unsubscribe from the listener when the component unmounts
          unsubscribe();
        };
      }, [navigate]);
    


  return (




    
    <div>

    {showLoginBox || showSignUpBox || showPasswordBox || showEmailBox ? (
      <div>
        <div className="blurred-background"></div>
   
      </div>
    ) : null}
      <div ref={popupRef}>
        {showLoginBox && (
          <LoginBox
            showSignUpBox={showSignUp}
            showPasswordBox={showPassword}
            showEmailBox={showEmail}
            onLoginSuccess={closeAllPopups}
          />
        )}
        {showSignUpBox && (
          <SignUpBox
            onGoBack={handleLoginButtonClick}
            onSignUpSuccess={() => {}}
            showPasswordBox={showPassword}
            closeAllPopups={closeAllPopups}
          />
        )}
        {showEmailBox && (
          <EmailBox
            onGoBack={handleLoginButtonClick}
            onSignUpSuccess={() => {}}
            showEmailBox={showEmailBox}
          />
        )}
        {showPasswordBox && <PasswordBox onGoBack={handleLoginButtonClick} />}
      </div>
    
      <div className="container">
        <div className="row text-center mt-5">
          <h1 className="display-3 fw-bold mt-5">Become an Expert</h1>
       
        </div>
 
        <div className="row pt-2 pb-2 mt-0 mb-3">
          <div className="col-md-6 border-right">
            <div className="bg-white p-3">
              <h2 className="fw-bold text-capitalize text-center">
              Become a Tio Certified Expert and Watch Your Business Flourish as You Share Your Expertise              </h2>
            </div>
          </div>
          <div className="col-md-6">
            <div className="bg-white p-4 text-start">
              <p className="fw-light">
              Becoming a Tio certified expert is a straightforward process. 
              The level of scrutiny you'll undergo depends on the specific field in which you wish to provide your expertise. 
              For instance, if you want to help others in areas like health or finance, a higher level of scrutiny will be applied to ensure the highest standards of expertise.
                </p>
                </div>
          </div>
          <div className="row text-center mt-5">
          <h2 className="fw-bold lead mb-3" style={{ fontSize: '2rem' }}>The benefits of being certified</h2>
          <div className="heading-line"></div>
        </div>
        </div>
      </div>
      {/* START THE CONTENT FOR THE SERVICES */}
      <div className="container">
        {/* START THE MARKETING CONTENT */}
        <div className="row align-items-end">
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 services mt-4">
            <div className="services__content">
            <div className="icon d-block fas fa-handshake mb-3" style={{ textAlign: 'left'}}></div>
              <h3 className="display-3--title mt-1">Unlock New Opportunities</h3>
              <p className="lh-lg">
              Certification isn't just a badge; it's a ticket to some fantastic opportunities. 
              Businesses and individuals looking for experts will be reaching out to you. 
              You can turn your passion into profit and build your reputation in a relaxed and easy-going way. 
              </p>
           
            </div>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 services mt-4 text-end">
            <div className="services__pic">
            <img src={`${process.env.PUBLIC_URL}/images/Tios/Growing-Tio.webp`} alt="TIO illustration" className="img-fluid3" />

            </div>
          </div>
        </div>
        {/* START THE WEB DEVELOPMENT CONTENT */}
        <div className="row">
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 services mt-4 text-start">
            <div className="services__pic">
            <img src={`${process.env.PUBLIC_URL}/images/Tios/Teaching-Tio.webp`} alt="TIO illustration" className="img-fluid2" />
            </div>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 services mt-4">
            <div className="services__content">
              <div className="icon d-block fas fa-unlock mb-3"style={{ textAlign: 'left'}}></div>
              <h3 className="display-3--title mt-1">Direct User Interaction</h3>
              <p className="lh-lg">
              As a certified expert, you get to chat directly with curious users.
               You'll be their trusted guide, helping them out, and it's pretty cool.
              </p>
            
            </div>
          </div>
        </div>
        {/* START THE CLOUD HOSTING CONTENT */}
        <div className="row">
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 services">
            <div className="services__content">
              <div className="icon d-block fas fa-person-rays mb-3" style={{ textAlign: 'left'}}></div>
              <h3 className="display-3--title mt-1">More Visibility</h3>
              <p className="lh-lg">
              Certification gives your profile a boost, making you easy to find for those who need your expertise.
               It's like your own personal spotlight!
              </p>
          
            </div>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 services mt-4 text-end">
            <div className="services__pic">
            <img src={`${process.env.PUBLIC_URL}/images/Tios/Baking.webp`} alt="TIO illustration" className="img-fluid2" />
            </div>
          </div>
          
        </div>
        <div className="container">
        <div className="heading-line"></div>
        <div className="row text-center mt-5">
  <h2 className="fw-bold lead mb-3" style={{ fontSize: '2rem' }}>Ready to Unlock the Benefits of Certification?</h2>
  <p>Join our community of certified experts and make a difference!</p>
  <div className="mb-3 col-md-5 mx-auto">
     {/* Adjust the width as needed */}
    <button type="button" 
    onClick={handleLoginButtonClick}
    className="btn btn-primary rounded-pill pt-3 pb-3 w-100">Sign Up</button>
  </div>
</div>

            </div>
        
      </div>
    </div>
  );
};

export default BecomeAnExpert;
